@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif !important;
  background-image: url(/static/media/bg.6d4c3e47.png) !important;
  background-attachment: fixed;
}

html, body {
  overflow-x: hidden;
  height: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

iframe {
  overflow: hidden;
  border: 0;
  align-self: center;
  width: 100%;
  height: 360px;
}
